import React from "react";
import  "./Card.css"
import { data } from "./data";
import { CardText, CardTitle, CardImg, CardImgOverlay, Card } from "reactstrap";

function Cards() {
  return (
    <div >
      <div className="">
        <div className="row justify-content-center   ">
          {data.map((e) => (
            <Card inverse key={e.id} className="card bg-dark col-lg-3  ">
              <CardTitle tag="h5" className="py-3 text-white">
                {e.title}
              </CardTitle>
              <CardImg alt={e.title} src={e.image} width="100%" height="80%" />
              <CardImgOverlay >
                <CardText className=" overlay">{e.desc}</CardText>
              </CardImgOverlay>
            </Card>
          ))}
        </div>
      </div>
    </div>
  );
}

export default Cards;
